/* Global css */

body,
html,
input,
textarea,
h1,
h2,
h3,
h4,
h5,
ul,
li,
p {
  padding: 0px;
  margin: 0px;
  /* font-family: Inknut Antiqua; 6.08*/
}

h2,
h3 {
  font-family: 'Inknut Antiqua', ExtraBold;
}

h3 {
  font-size: 14px;
  margin-top: -25px;
}

img {
  width: 100%;
}

/* Homepage css */

.bodycontainer {
  margin: 0px auto;
  position: relative;
  /*background-color: rgb(250, 250, 250);*/
  background-position: 0 0;
  background-repeat: no-repeat;
}
.bodycontainer::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-position: center top;
  background-size: cover;
  opacity: 0.2;
  z-index: 0;
}
.bodywrapper {
  position: relative;
  padding: 5%;
  padding-bottom: 0;
}

.headercontainer {
  width: 100%;
  padding: 0;
}

.homelogo {
  width: 35%;
  text-align: center;
  margin: 0px auto 0px auto;
}
/*
.header-heading {
  position: relative;
  z-index: 1;
}*/
.footer-component {
  z-index: 1;
  height: 125px;
  margin-left: 0;
  margin-right: 0;
  margin-bottom: 0;
}

/* Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) {
  .bodycontainer {
    width: 100%;
  }
}

/* Medium devices (tablets, 768px and up) */
@media (min-width: 768px) {
  .bodycontainer {
    width: 50%;
  }
}

/* Large devices (desktops, 992px and up) */
@media (min-width: 992px) {
  .bodycontainer {
    width: 40%;
  }
}

/* Extra large devices (large desktops, 1200px and up) */
@media (min-width: 1200px) {
  .bodycontainer {
    width: 375px;
  }
}
